<template>
  <div>
    <div class="d-flex justify-content-end">
      <button
        class="btn btn-primary btn-icon btn-round"
        style="position: absolute; z-index: 100"
        @click="
          () => {
            modal.mini = true;
            readyForm();
          }
        "
      >
        <i class="tim-icons icon-pencil"></i>
      </button>
    </div>
    <modal
      :show.sync="modal.mini"
      class="modal-primary"
      :show-close="false"
      headerClasses="justify-content-center"
      type="mini"
    >
    <alert type="danger" dismissible :visible="visible" :dismissAlert="dismissAlert">
        <!-- <div class="alert-icon">
          <i class="now-ui-icons objects_support-17"></i>
        </div> -->
        <strong>Error al Enviar Los Datos</strong> <template>Compruebe Los Campos</template>
    </alert>
      <Card class="card-form mb-0" title="Editar Perfil">
        <form v-on:submit.prevent="changeName()">
          <div class="form-group">
            <label for="example">Nombre</label>
            <input
              type="name"
              class="form-control dash"
              placeholder="Ingrese Nombre"
              v-model="name"
            />
          </div>
          <div class="row justify-content-center">
            <button type="submit" class="btn btn-primary">Submit</button>
          </div>
        </form>
        <form v-on:submit.prevent="changePass()">
          <div class="form-group">
            <label for="example">Contraseña Actual</label>
            <input
              type="password"
              class="form-control dash"
              aria-label="Default select example"
              placeholder="Ingrese Contraseña Actual"
              v-model="pass.old_password"
            />
          </div>
          <div class="form-group">
            <label for="example">Nueva Contraseña</label>
            <input
              type="password"
              class="form-control dash"
              aria-label="Default select example"
              placeholder="Ingrese Nueva Contraseña"
              v-model="pass.new_password"
            />
          </div>
          <!-- <div class="form-group">
            <label for="example">Repertir Nueva Contraseña</label>
            <input
              type="password"
              class="form-control dash"
              aria-label="Default select example"
              placeholder="Ingrese Confirmacion De Nueva Contraseña"
            />
          </div> -->
          <div class="row justify-content-center">
            <a
              class="btn btn-neutral"
              v-on:click="
                modal.mini = false;
                resetForm();
              "
              style="color: #f96332"
              >Cerrar</a
            >
            <button type="submit" class="btn btn-primary">Submit</button>
          </div>
        </form>
      </Card>
    </modal>
  </div>
</template>

<script>
import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import {userStore} from '@/store/userStore';
import {
  DropDown,
  Navbar,
  NavLink,
  Modal,
  Button,
  FormGroupInput,
  Card,
} from "@/components";
import UsersService from "../../../services/UsersService";
import Alert from "../../Alert.vue";

export default {
  name: "FormPassword",
  props: {
    nameUser: String,
    reloadList: { type: Function },
    listCompanies: [],
  },
  components: {
    Card,
    Modal,
    [FormGroupInput.name]: FormGroupInput,
    [Button.name]: Button,
    Alert
},
  setup: () => ({ v$: useVuelidate() }),
  data: () => {
    return {
      modal: {
        mini: false,
      },
      pass: {
        old_password: "",
        new_password: "",
      },
      name: "",
      visible: false,
      store: userStore()
    };
  },
  validations() {
    return {
      pass: {
        old_password: { required },
        new_password: { required },
      },
      name: { required },
    };
  },
  methods: {
    resetForm() {
      this.pass = {
        old_password: "",
        new_password: "",
      };
      this.name = this.nameUser;
    },
    readyForm() {
      this.resetForm();
    },
    async changePass() {
      this.v$.pass.$touch();
      if (!this.v$.pass.$invalid) {
        const response = await UsersService.changePass(this.pass).catch(
          function (error) {
            alert(JSON.stringify(error.response.data));
          }
        );
        if (response) {
          this.modal.mini = false;
        }
      } else {
        this.visible=true;
      }
    },
    async changeName() {
      this.v$.name.$touch();
      if (!this.v$.name.$invalid) {
        const response = await UsersService.changeName(this.name).catch(
          function (error) {
            
            alert(JSON.stringify(error.response.data));
          }
        );
        if (response) {
          this.modal.mini = false;
          this.store.setName(this.name);   
          // location.reload()
        }
      } else {
        this.visible=true;
      }
    },
    dismissAlert(){
      this.visible=false;
    }
  },
  created() {
    if(!this.store.getName()){
      this.modal.mini=true;
    }
  },
};
</script>
