<template>
  <div class="row">
    <div class="col-12">
      <card>
        <div class="text-center">
          <h4 style="color: #ffffff" class="mt-2">Empresas <small>Lista</small></h4>
        </div>
        <form-company :reload-list="getCompanies"></form-company>
        <div class="table-responsive" style="overflow-y: hidden; overflow-x: auto;">
          <base-table :columns="table1.columns" thead-classes="text-primary">
            <slot>
              <tr v-for="item in listCompanies" :key="item.id">
                <td>
                  {{ item.id }}
                </td>
                <td>
                    {{ item.name }}
                </td>
                <td><img :src="item.logo" style="width:50px"></td>
                <td>
                  {{ size[item.size - 1] }}
                </td>
                <td>
                  {{ FormatDate(item.created_date) }}
                </td>
                <td>
                  <button
                    v-on:click="$router.push({ name: 'Detalles Empresa', params: { id: item.id } })"
                    class="btn btn-warning btn-icon btn-round ml-1"
                    ><i class="now-ui-icons files_single-copy-04"></i
                  ></button>
                  <button class="btn btn-info btn-icon btn-round ml-1" v-on:click="editCompany(item)"
                    ><i class="now-ui-icons ui-1_settings-gear-63"></i
                  ></button>
                  <button class="btn btn-danger btn-icon btn-round ml-1" v-on:click="deleteCompany(item.id)" v-bind:disabled="isLinked(item.id)">
                    <i class="now-ui-icons ui-1_simple-remove"></i
                  ></button>
                </td>
              </tr>
            </slot>
          </base-table>
        </div>
      </card>
    </div>
  </div>
</template>
<script>
import { BaseTable } from "@/components";
import FormCompany from "../components/DASHBOARD/Form/FormCompany.vue";
import CompaniesService from "../services/CompaniesService";
import {userStore} from '@/store/userStore';
const tableColumns = [
  "#",
  "nombre",
  "logo",
  "tamaño",
  "fecha Creacion",
  "acciones",
];

export default {
  components: {
    BaseTable,
    FormCompany,
  },
  data() {
    return {
      table1: {
        title: "Simple Table",
        columns: [...tableColumns],
      },
      listCompanies: [],
      size: ["Grande", "Mediana","Pequeña"],
      store: userStore()
    };
  },
  methods: {
    async getCompanies() {
      this.listCompanies = await CompaniesService.getCompanies(
      );
      this.listCompanies = this.listCompanies.data;
    },
    async deleteCompany(id) {
      const response = await CompaniesService.deleteCompany(
        id
      );
      if (response) {
        this.getCompanies()
      }
    },
    editCompany(item){
      CompaniesService.updateProp(item)
      document.getElementById('addCompanyButton').click();
    },
    FormatDate(date) {
      date = date.split("T");
      date = date[0].split("-").reverse().join("-");
      return date;
    },
    isLinked(id){
      return Boolean(this.store.getCompany()==id)
    }
  },
  created() {
    this.getCompanies();
  },
};
</script>
<style></style>
