<template>
  <div>
    <div class="row justify-content-end">
      <div class="mr-5">
        <button
          id="addProjectButton"
          class="btn btn-primary"
          href="javascript:void(0)"
          @click="
            () => {
              modal.mini = true;
              readyForm();
            }
          "
        >
          Agregar Proyecto
        </button>
      </div>
    </div>
    <modal
      :show.sync="modal.mini"
      class="modal-primary"
      :show-close="false"
      headerClasses="justify-content-center"
      type="mini"
      @close="
      () => {
        resetForm()
      }
    "
    >
      <alert type="danger" dismissible :visible="visible" :dismissAlert="dismissAlert">
          <!-- <div class="alert-icon">
            <i class="now-ui-icons objects_support-17"></i>
          </div> -->
          <strong>Error al Ingresar Proyecto</strong> <template>Compruebe Los Campos</template>
      </alert>
      <Card class="card-form mb-0" :title="titles[1] + ' Proyecto'">
        <form v-on:submit.prevent="HandleProject"> 
          <div class="form-group">
            <label for="example">Nombre</label>
            <input
              type="nombre"
              class="form-control dash"
              placeholder="Ingrese Nombre"
              v-model="project.name"
            />
          </div>
          <div class="form-group">
            <label for="example">Valor</label>
            <input
              type="number"
              class="form-control dash"
              placeholder="Ingrese Valor en UF"
              v-model="project.value"
            />
          </div>
          <div class="form-group">
            <label for="example">Empresa</label>
            <select
              class="form-control dash"
              aria-label="Default select example"
              v-model="project.company"
            >
              <option value="">Ingrese Empresa</option>
              <option v-for="item in listCompanies" :key="item.id" :value="item.id">{{item.name}}</option>
            </select>
          </div>
          <div class="form-group">
            <label for="example">Documento de Requimientos</label>
            <input
              type="file"
              class="form-control dash"
              placeholder="Adjunte Documento"
              @change="handleFile"
              ref="fileReqs"
            />
          </div>
          <div class="form-group">
            <label for="example">Descripcion</label>
            <textarea
              class="form-control dash"
              placeholder="Ingrese Descripcion"
              v-model="project.smalldescription"
            />
          </div>
          <div class="form-group">
            <label for="example">Descripcion Detallada</label>
            <textarea
              class="form-control dash"
              placeholder="Ingrese Descripcion"
              rows="25"
              v-model="project.description"
            />
          </div>
          <div class="row justify-content-center">
            <a class="btn btn-neutral" v-on:click="modal.mini=false; resetForm()" style="color:#f96332">Cerrar</a>
            <button type="submit" class="btn btn-primary">Submit</button>
          </div>
        </form>
      </Card>
    </modal>
  </div>
</template>
<script>
import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import { Modal, Button, FormGroupInput, Card , Alert} from "@/components";
import ProjectsService from "../../../services/ProjectsService";
export default {
  name: "FormProject",
  props: {
    name: String,
    listCompanies:[],
    reloadList: { type: Function },
  },
  components: {
    Card,
    Modal,
    [FormGroupInput.name]: FormGroupInput,
    [Button.name]: Button,
    Alert
  },
  setup: () => ({ v$: useVuelidate() }),
  data: () => {
    return {
      submitt: false,
      project: {
        id: "",
        name: "",
        value: "",
        file: File,
        company: "",
        description: "",
        smalldescription: "",
      },
      modal: {
        mini: false,
      },
      titles: ["Editar", "Agregar Nuevo"],
      visible: false,
    };
  },
  validations() {
    return {
      project: {
        id: {},
        name: { required },
        value: { required },
        company: { required },
        description: {},
        descriptionDetail: {},
        file: {},
      },
    };
  },
  methods: {
    handleFile(){
      this.project.file=this.$refs.fileReqs.files
    },
    resetForm() {
      ProjectsService.resetProp()
    },
    readyForm() {
      this.$refs.fileReqs.value=null;
      this.project = ProjectsService.getProp()
    },
    async addProject() {
      const response =await ProjectsService.addProject(
        this.project
      )
      if(response){
        this.reloadList()
      }
    },
    async updateProject() {
      const response =await ProjectsService.updateProject(this.project)
      if(response){
        this.reloadList()
      }
    },
    HandleProject() {
      this.v$.$validate();
      this.submitt = true;
      if (!this.v$.$error) {
        if (!this.project.id) {
          this.addProject()
        } else {
          this.updateProject()
        }
        this.modal.mini=false
        this.resetForm();
      } else {
        // alert(this.v$.user.email.$error[0].$message)
        this.visible=true
        // 
      }
    },
    dismissAlert(){
      this.visible=false;
    },
  },
};
</script>
<style></style>
