<template>
  <div class="row">
    <div class="col-12">
      <card>
        <div class="text-center">
          <h4 style="color: #ffffff" class="mt-2">
            <template>{{ project.name }} <small>Etapas</small></template>
          </h4>
        </div>
        <div class="table-responsive" style="overflow: hidden">
          <base-table :columns="table1.columns" thead-classes="text-primary">
            <slot>
              <tr v-for="item in listEtapes" :key="item.id">
                <td>
                  {{ item.id }}
                </td>
                <td>
                  <template v-if="!item.name"> A la Espera </template>
                  {{ item.name }}
                </td>
                <td>
                <a href="">
                  <i class="tim-icons icon-book-bookmark"></i>
                </a>
                </td>
                <td>{{ item.time }} Semanas</td>
                <td>
                  {{ FormatDate(item.created_date) }}
                </td>
                <td>
                  <template v-if="item.is_finished">
                    <div class="progress" style="width: 50%">
                      <div
                        class="progress-bar progress-bar-striped progress-bar-animated bg-success"
                        role="progressbar"
                        aria-valuenow="75"
                        aria-valuemin="0"
                        aria-valuemax="100"
                        style="width: 100%"
                      ></div>
                    </div>
                  </template>
                  <template v-else>
                    <div class="progress" style="width: 50%">
                      <div
                        class="progress-bar progress-bar-striped progress-bar-animated bg-success"
                        role="progressbar"
                        aria-valuenow="75"
                        aria-valuemin="0"
                        aria-valuemax="100"
                        style="width: 0%"
                      ></div></div
                  ></template>
                </td>
                <td>
                  <template v-if="store.getRole() <= 2">
                    <!-- <button
                      v-if="!idCompany"
                      class="btn btn-info btn-icon btn-round"
                      v-on:click="editCompany(item)"
                    >
                      <i class="now-ui-icons ui-1_settings-gear-63"></i>
                    </button> -->
                    <button
                      class="btn btn-danger btn-icon btn-round ml-1"
                      v-on:click="deleteEtape(item.id)"
                      v-bind:disabled="isLinked(item.id)"
                    >
                      <i class="now-ui-icons ui-1_simple-remove"></i>
                    </button>
                  </template>
                </td>
              </tr>
            </slot>
          </base-table>
        </div>
      </card>
    </div>
  </div>
</template>
<script>
import { BaseTable } from "@/components";
import EtapesService from "@/services/EtapesService.js";
import {userStore} from '@/store/userStore';
const tableColumns = [
  "#",
  "Nombre",
  "Descripcion",
  "Duracion Esperada",
  "Fecha Creacion",
  "Progreso",
  "Acciones",
];

export default {
  props: { project: {} },
  components: {
    BaseTable,
  },
  data() {
    return {
      table1: {
        columns: [...tableColumns],
      },
      listEtapes: [],
      store : userStore()
    };
  },
  methods: {
    async getEtapes() {
      this.listEtapes = await EtapesService.getEtapes(this.$route.params.id);
      this.listEtapes = this.listEtapes.data;
      
    },
    async deleteEtape(id) {
      const response = await EtapesService.deleteEtape(id);
      if (response) {
        this.getEtapes();
      }
    },
    FormatDate(date) {
      date = date.split("T");
      date = date[0].split("-").reverse().join("-");
      return date;
    },
    isLinked(id) {
      return Boolean(this.store.getId == id);
    },
  },
  created() {
    this.getEtapes();
  },
};
</script>
