<template>
  <div>
    <div class="row">
      <div class="col-6">
        <card v-if="company">
          <div class="text-center">
            <h4 style="color: #ffffff" class="mt-2">
              {{ company.name }} <small>Informacion</small>
            </h4>
          </div>
          <div class="container-fluid" style="color: rgb(255 255 255 / 73%)">
            {{ company.description }}
          </div>
        </card>
      </div>
      <div class="col-6">
        <card v-if="company">
          <div class="text-center">
            <h4 style="color: #ffffff" class="mt-2">
              {{ company.name }} <small>Contacto</small>
            </h4>
          </div>
          <div class="container-fluid" style="color: rgb(255 255 255 / 73%)">
            {{ company.contact }}
          </div>
        </card>
      </div>
    </div>
    <Project :idCompany="Number($route.params.id)"></Project>
    <Users :idCompany="Number($route.params.id)"></Users>
  </div>
</template>
<script>
//   import { BaseTable } from "@/components";
import CompaniesService from "../services/CompaniesService";
import Project from "./Projects.vue";
import Users from "./Users.vue";
const tableColumns = [
  "#",
  "Nombre",
  "Empresa",
  "Valor",
  "Requerimientos",
  "Fecha Creacion",
  "Acciones",
];

export default {
  components: {
    Project,
    Users
  },
  data() {
    return {
      listCompanies: [],
      company: {},
    };
  },
  methods: {
    async getCompanies() {
      this.listCompanies = await CompaniesService.getCompanies(
      );
      this.listCompanies = this.listCompanies.data;
      
    },
    findCompany(id) {
      for (const company of this.listCompanies) {
        if (company.id == id) {
          return company;
        }
      }
    },
    async loadParams() {
      await this.getCompanies();
      if (this.$route.params.id) {
        this.company = this.findCompany(this.$route.params.id);
      }
    },
  },
  created() {
    this.loadParams();
  },
};
</script>
