<template>
  <div>
    <div class="row justify-content-end">
      <div class="mr-5">
        <a
          class="btn btn-primary"
          href="javascript:void(0)"
          @click="
            () => {
              modal.mini = true;
              readyForm();
            }
          "
        >
          Agregar Usuario
        </a>
      </div>
    </div>
    <modal
      :show.sync="modal.mini"
      class="modal-primary"
      :show-close="false"
      headerClasses="justify-content-center"
      type="mini"
    >
    <alert type="danger" dismissible :visible="visible" :dismissAlert="dismissAlert">
        <!-- <div class="alert-icon">
          <i class="now-ui-icons objects_support-17"></i>
        </div> -->
        <strong>Error al Crear Usuario</strong> <template>Compruebe Los Campos</template>
    </alert>
      <Card class="card-form mb-0" :title="titles[1] + ' Usuario'">
        <form v-on:submit.prevent="HandleUser">
          <div class="form-group">
            <label for="example">Email</label>
            <input
              type="email"
              class="form-control dash"
              placeholder="Ingrese Email"
              v-model="user.email"
            />
          </div>
          <div class="form-group" v-if="rol == 1">
            <label for="example">Empresa</label>
            <select
              class="form-control dash"
              aria-label="Default select example"
              v-model="user.company"
            >
              <option value="">Ingrese Empresa</option>
              <option
                v-for="item in listCompanies"
                :key="item.id"
                :value="item.id"
              >
                {{ item.name }}
              </option>
            </select>
          </div>
          <div class="form-group" v-if="rol == 1">
            <label for="example">Tipo de Usuario</label>
            <select
              class="form-control dash"
              aria-label="Default select example"
              v-model="user.rol"
            >
              <option value="">Ingrese Tipo De Usuario</option>
              <option value="2">Admin</option>
              <option value="3">Usuario Normal</option>
            </select>
          </div>
          <div class="row justify-content-center">
            <a
              class="btn btn-neutral"
              v-on:click="
                modal.mini = false;
                resetForm();
              "
              style="color: #f96332"
              >Cerrar</a
            >
            <button type="submit" class="btn btn-primary">Submit</button>
          </div>
        </form>
      </Card>
    </modal>
  </div>
</template>

<script>
import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import {
  DropDown,
  Navbar,
  NavLink,
  Modal,
  Button,
  FormGroupInput,
  Card, Alert
} from "@/components";
import UsersService from "../../../services/UsersService";
// import CompaniesService from '@/services/CompaniesService'
import {userStore} from '@/store/userStore';
export default {
  name: "FormUser",
  props: {
    name: String,
    reloadList: { type: Function },
    listCompanies: [],
  },
  components: {
    Card,
    Modal,
    [FormGroupInput.name]: FormGroupInput,
    [Button.name]: Button,
    Alert
  },
  setup: () => ({ v$: useVuelidate() }),
  data: () => {
    return {
      user: {
        email: "",
        company: "",
        rol: "",
      },
      modal: {
        mini: false,
      },
      titles: ["Editar", "Agregar Nuevo"],
      rol: 3,
      visible:false,
      store: userStore()
    };
  },
  validations() {
    return {
      user: {
        rol: { required },
        email: { required },
        company: { required },
      },
    };
  },
  methods: {
    resetForm() {
      this.user = {
        email: "",
        company: "",
        rol: "",
      };
    },
    readyForm() {
      this.resetForm();
    },
    async addUser() {
      const response = await UsersService.addUser(this.user);
      if (response) {
        this.reloadList();
      }
    },
    HandleUser() {
      if (this.rol == 2) {
        this.user.company = this.$session.get("company");
        this.user.rol = 3;
      }
      this.v$.$validate();
      if (!this.v$.$error) {
        this.addUser();
        this.modal.mini = false;
      } else {
        this.visible=true;
        
      }
    },
    dismissAlert(){
      this.visible=false;
    }
  },
  created() {
    this.rol = this.store.getRole();
  },
};
</script>
