import Api from "./Api";

let company = {
  id: '',
  name: '',
  size: '',
  description: '',
  contact: '',
  logo: '',
}
export default {
  getCompanies() {
    // let listUsers = []
    return Api(1).get('companies/')
  },
  addCompany(name, size, description, contact, logo) {
    return Api(1,1).post('companies/', {
      name,
      size,
      description,
      logo
    })
  },
  getProp() {
    return Object.assign({} , company)
  },
  updateProp(aux) {
    company.id = aux.id
    company.name = aux.name
    company.description = aux.description
    company.size = aux.size
    // compa
  },
  resetProp() {
    company = {
      id: '',
      name: '',
      size: '',
      description: '',
      contact: '',
      logo: '',
    }
    
  },
  deleteCompany(id) {
    return Api(1).delete('companies/' + id)
  },
  updateCompany(id, name, size, description, logo) {
    return Api(1,1).put('companies/' + id+'/', {
      name,
      size,
      description,
      logo
      // contact_id
    })
  },
}
