import axios from "axios";

export default (token, multiPart) => {
  const storeData = JSON.parse(localStorage.getItem("Info"));
  let configAxiosToken = { baseURL: process.env.VUE_APP_BASE_API, headers: {} };
  if (token) {
    let configAxiosApi = {
      baseURL: process.env.VUE_APP_BASE_API,
      headers: {
        "Content-Type": "application/json",
        Authorization: "Token " + storeData.token,
      },
    };
    if (multiPart) {
      configAxiosApi.headers["Content-Type"]='multipart/form-data'
      };
      return axios.create(configAxiosApi)
    }
  return axios.create(configAxiosToken);
};
