<template>
  <div class="row">
    <div class="col-12">
      <card>
        <div class="text-center">
          <h4 style="color: #ffffff" class="mt-2">
            <template v-if="idCompany"
              >{{ company.name }} <small>Usuarios</small>
            </template>
            <template v-else>Usuarios <small>Lista</small></template>
          </h4>
        </div>
        <form-user v-if="!idCompany" :listCompanies="listCompanies" :reload-list="getUsers"></form-user>
        <div class="table-responsive" style="overflow: hidden">
          <base-table :columns="table1.columns" thead-classes="text-primary">
            <slot>
              <tr v-for="item in listUsers" :key="item.id">
                <td>
                  {{ item.id }}
                </td>
                <td>
                  <template v-if="!item.name">
                    A la Espera
                  </template>
                    {{ item.name }}
                </td>
                <td>
                  <img
                    v-if="findCompany(item.company)"
                    :src="findCompany(item.company).logo"
                    :alt="findCompany(item.company).name"
                    style="width: 50px"
                  />
                </td>
                <td>
                  {{ item.email }}
                </td>
                <td>
                  {{ FormatDate(item.date_joined) }}
                </td>
                <td>
                  {{ rol[item.rol - 1] }}
                </td>
                <td>
                  <template v-if="item.last_login">{{
                    FormatDate(item.last_login)
                  }}</template>
                </td>
                <td>
                  <template v-if="store.getRole() <= 2">
                    <!-- <button
                      v-if="!idCompany"
                      class="btn btn-info btn-icon btn-round"
                      v-on:click="editCompany(item)"
                    >
                      <i class="now-ui-icons ui-1_settings-gear-63"></i>
                    </button> -->
                    <button
                      class="btn btn-danger btn-icon btn-round ml-1"
                      v-on:click="deleteUser(item.id)"
                      v-bind:disabled="isLinked(item.id)"
                    >
                      <i class="now-ui-icons ui-1_simple-remove"></i>
                    </button>
                  </template>
                </td>
              </tr>
            </slot>
          </base-table>
        </div>
      </card>
    </div>
  </div>
</template>
<script>
import { BaseTable } from "@/components";
import FormUser from "../components/DASHBOARD/Form/FormUser.vue";
import CompaniesService from "../services/CompaniesService";
import UsersService from "../services/UsersService";
import {userStore} from '@/store/userStore';
const tableColumns = [
  "#",
  "Nombre",
  "Empresa",
  "Email",
  "Fecha Creacion",
  "Rol",
  "Ultimo Login",
  "Acciones",
];

export default {
  props: { idCompany: Number },
  components: {
    BaseTable,
    FormUser,
  },
  data() {
    return {
      table1: {
        columns: [...tableColumns],
      },
      listUsers: [],
      listCompanies: [],
      company: {},
      rol: ["Super Admin", "Admin", "Usuario Normal"],
      store: userStore()
    };
  },
  methods: {
    async getUsers() {
      this.listUsers = await UsersService.getUsers(this.idCompany);
      this.listUsers = this.listUsers.data;
    },
    async getCompanies() {
      this.listCompanies = await CompaniesService.getCompanies();
      this.listCompanies = this.listCompanies.data;
    },
    async deleteUser(id){
      const response = await UsersService.deleteUser(id)
      if (response){
        this.getUsers()
      }
    },
    findCompany(id) {
      for (const company of this.listCompanies) {
        if (company.id == id) {
          return company;
        }
      }
    },
    FormatDate(date) {
      date = date.split("T");
      date = date[0].split("-").reverse().join("-");
      return date;
    },
    async loadProps() {
      await this.getCompanies();
      if (this.idCompany) {
        this.company = this.findCompany(this.idCompany);
      }
    },
    isLinked(id){
      return Boolean(this.store.getId()==id)
    },
  },
  created() {
    this.loadProps();
    this.getUsers();
  },
};
</script>
<style>
.table-responsive{
  overflow: hidden !important;
}
</style>
