<template>
  <div>
    <div class="row">
      <div class="col-6">
        <card v-if="$route.params.id">
          <div class="text-center">
            <h4 style="color: #ffffff" class="mt-2">
              {{ project.name }} <small>Informacion</small>
            </h4>
          </div>
          <div class="container-fluid" style="color: rgb(255 255 255 / 73%)">
            {{ project.description }}
          </div>
        </card>
      </div>
      <div class="col-6" style="margin-bottom: 30px">
        <FunctionalCalendar
          :is-dark="true"
          :marked-date-range="markedDateRange"
        ></FunctionalCalendar>
      </div>
    </div>
    <Etapes :project="project"/>
  </div>
</template>
<script>
import ProjectsService from "../services/ProjectsService";
import { FunctionalCalendar } from "vue-functional-calendar";
import Etapes from "./Etapes.vue";
const tableColumns = [
  "#",
  "Nombre",
  "Empresa",
  "Valor",
  "Requerimientos",
  "Fecha Creacion",
  "Acciones",
];
export default {
  components: {
    FunctionalCalendar,
    Etapes,
  },
  data() {
    return {
      listProjects: [],
      project: {},
      calendarData: {},
      calendarConfigs: {
        sundayStart: false,
        dateFormat: "dd/mm/yyyy",
        isDatePicker: false,
        isDateRange: false,
      },
      markedDateRange: { start: "11/11/2022", end: "17/11/2022" },
    };
  },
  methods: {
    async getProjects() {
      this.listProjects = await ProjectsService.getProjects();
      this.listProjects = this.listProjects.data;
      
    },
    findProject(id) {
      for (const project of this.listProjects) {
        if (project.id == id) {
          return project;
        }
      }
    },
    async loadParams() {
      await this.getProjects();
      if (this.$route.params.id) {
        this.project = this.findProject(this.$route.params.id);
        
      }
    },
  },
  created() {
    this.loadParams();
  },
};
</script>
<style>
.vfc-calendar {
  background: #27293d !important;
  font-family: "Montserrat", "Helvetica Neue", Arial, sans-serif;
}
</style>
