            href=""
            target="_blank"
          >
            <i class="fab fa-facebook-square"></i>
            <p class="d-lg-none d-xl-none">Facebook</p>
          </a>
        </li>
        <li class="nav-item">
          <a
            class="nav-link"
            rel="tooltip"
            title="Follow us on Instagram"
            data-placement="bottom"
            href=""
            target="_blank"
          >
            <i class="fab fa-instagram"></i>
            <p class="d-lg-none d-xl-none">Instagram</p>
          </a>
        </li>
      </template>
    </navbar>
    <modal
      :show.sync="modals.mini"
      class="modal-primary"
      :show-close="false"
      headerClasses="justify-content-center"
      type="mini"
    >
    <alert type="danger" dismissible :visible="visible" :dismissAlert="dismissAlert">
        <!-- <div class="alert-icon">
          <i class="now-ui-icons objects_support-17"></i>
        </div> -->
        <strong>Error al Iniciar Sesion</strong> <template>Compruebe Correo y Contraseña</template>
    </alert>
      <card
        class="card-signup"
        header-classes="text-center"
        color="orange"
        style="
          background: linear-gradient(0deg,rgb(255 151 151 / 12%) 0%,rgba(249, 100, 50, 0.58) 100%);
        "
      >
        <template slot="header">
          <div class="modal-profile" style="margin: 0 auto">
            <i class="now-ui-icons users_circle-08"></i>
          </div>
          <h3
            class="card-title title-up"
            style="font-size: 1.25em; font-weight: 400"
          >
            Login
          </h3>
        </template>
        <template>
          <form v-on:submit.prevent="LogIn()">
            <fg-input
              class="no-border"
              placeholder="Email"
              addon-left-icon="now-ui-icons ui-1_email-85"
            >
              <input
                slot="input"
                class="form-control"
                placeholder="Email"
                v-model="user.name"
                aria-describedby="addon-right addon-left"
              />
            </fg-input>
            <fg-input
              class="no-border"
              placeholder="Contrasena"
              addon-left-icon="now-ui-icons text_caps-small"
            >
              <input
                type="password"
                slot="input"
                class="form-control"
                placeholder="Contraseña"
                v-model="user.password"
                aria-describedby="addon-right addon-left"
              />
            </fg-input>
            <div class="text-center mb-4" style="font-size: 0.8em">
              <div style="height: 30px"></div>
              <p>
                ¿Se te olvido la Contraseña?
                <a
                  href="http://127.0.0.1:8000/api/auth/accounts/password_reset/"
                  target="_blank"
                  ><strong>Click aqui</strong></a
                >
              </p>
              <n-button
                type="default"
                size="lg"
                round
                v-on:click="modals.mini = false"
                >Cerrar</n-button
              >
              <button class="btn btn-round btn-lg btn-neutral" type="submit">
                Iniciar
              </button>
            </div>
          </form>
        </template>
