    </modal>
  </div>
</template>
<script>
import {
  DropDown,
  Navbar,
  NavLink,
  Modal,
  Button,
  FormGroupInput,
  Card,
} from "@/components";
import { Popover } from "element-ui";
import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import UsersService from "../services/UsersService";
import { Alert } from "@/components";
import { userStore } from "@/store/userStore"
export default {
  name: "main-navbar",
  props: {
    transparent: Boolean,
    colorOnScroll: Number,
  },
  components: {
    Card,
    Navbar,
    [Button.name]: Button,
    [Popover.name]: Popover,
    [FormGroupInput.name]: FormGroupInput,
    Modal,
    Alert,
  },
  setup: () => ({ v$: useVuelidate() ,store: userStore()
} ),
  data() {
    return {
      modals: {
        mini: false,
      },
      user: {
        name: "",
        password: "",
      },
      visible: false,
    };
  },
  validations() {
    return {
      user: {
        name: { required },
        password: { required },
      },
    };
  },
  methods: {
    async LogIn() {
      this.v$.$validate();
      if (!this.v$.$error) {
        try {
          const response = await UsersService.getToken(
            this.user.name,
            this.user.password
          );
          if (response) {
            this.modals.mini = false;
            this.store.setInfo(response.data.user.id,response.data.user.name,response.data.user.rol,response.data.user.company);
            this.store.setToken(response.data.token);
            // this.$session.set("rol", response.data.user.rol);
            // this.$session.set("token", response.data.token);
            // this.$session.set("company", response.data.user.company);
            // this.$session.set("idUser", response.data.user.id);
            setTimeout(() => {
              if (!response.data.user.name) {
                this.$router.push("/management/profile");
              } else {
                this.$router.push("/management");
              }
            }, 30);
          }
        } catch (response) {
          this.visible=true;
        }
      } else {
        this.visible=true;
      }
    },
    dismissAlert(){
      this.visible=false;
    }
  },
};
</script>

