import Api from "./Api";
export default {
  getToken(name,password) {
    return Api().post("/auth/token/login/", {
      "email": name,
      "password": password,
    });
  },
  getUsers(id){
    if (id){
      return Api(1).get('auth/users/'+id+'/company')
    }
    return Api(1).get("auth/users/");
  },
  addUser(user){
    return Api(1).post('auth/users/',user)
  },
  deleteUser(id){
    return Api(1).delete('auth/users/'+id)
  },
  logOut(){
    return Api(1).post('auth/token/logoutall/')
  },
  personalInfo(){
    return Api(1).get('auth/users/personal')
  },
  changePass(password){
    return Api(1).put('auth/users/change_password/',password)
  },
  changeName(name){
    return Api(1).put('auth/users/change_name/',{name})
  }
};
