<template>
  <div class="row">
    <div class="col-12">
      <card>
        <div class="text-center">
          <h4 style="color: #ffffff" class="mt-2">
            <template v-if="idCompany"
              >{{ company.name }} <small>Proyectos</small>
            </template>
            <template v-else>Proyectos <small>Lista</small></template>
          </h4>
        </div>
        <FormProject
          v-if="!idCompany && rol == 1"
          :idCompany="idCompany"
          :listCompanies="listCompanies"
          :reload-list="getProjects"
        ></FormProject>
        <div
          class="table-responsive"
        >
          <base-table :columns="table1.columns" thead-classes="text-primary">
            <slot>
              <tr v-for="item in listProjects" :key="item.id">
                <td>
                  {{ item.id }}
                </td>
                <td>
                  <a style="cursor: pointer">
                    {{ item.name }}
                  </a>
                </td>
                <!-- <td><img :src="item.logo" style="width: 50px" /></td> -->
                <td>
                  <img
                    v-if="findCompany(item.company)"
                    :src="findCompany(item.company).logo"
                    :alt="findCompany(item.company).name"
                    style="width: 50px"
                  />
                </td>
                <td>UF {{ item.value }}</td>
                <td>
                  <a :href="item.file" target="_blank" v-if="item.file">
                    <img
                      v-if="findType(item.file) == 'pdf'"
                      src="@/assets/icons/pdf.png"
                      style="width: 28px"
                      alt=""
                    />
                    <img
                      v-else
                      src="@/assets/icons/doc.png"
                      style="width: 28px"
                      alt=""
                    />
                    {{ findName(item.file) }}
                  </a>
                </td>
                <td>
                  {{ FormatDate(item.created_date) }}
                </td>
                <td>
                  <button class="btn btn-warning btn-icon btn-round ml-1" v-on:click="$router.push({ name: 'Detalles Proyecto', params: { id: item.id } })">
                    <i class="now-ui-icons files_single-copy-04"></i>
                  </button>
                  <template v-if="rol == 1">
                    <button
                      v-if="!idCompany"
                      class="btn btn-info btn-icon btn-round ml-1"
                      v-on:click="editProject(item)"
                    >
                      <i class="now-ui-icons ui-1_settings-gear-63"></i>
                    </button>
                    <button
                      class="btn btn-danger btn-icon btn-round ml-1"
                      v-on:click="deleteProject(item.id)"
                    >
                      <i class="now-ui-icons ui-1_simple-remove"></i>
                    </button>
                  </template>
                </td>
              </tr>
            </slot>
          </base-table>
        </div>
      </card>
    </div>
  </div>
</template>
<script>
import { BaseTable } from "@/components";
import ProjectsService from "../services/ProjectsService";
import CompaniesService from "../services/CompaniesService";
import FormProject from "../components/DASHBOARD/Form/FormProject.vue";
import {userStore} from '@/store/userStore';
const tableColumns = [
  "#",
  "Nombre",
  "Empresa",
  "Valor",
  "Requerimientos",
  "Fecha Creacion",
  "Acciones",
];

export default {
  props: { idCompany: Number },
  components: {
    BaseTable,
    FormProject,
  },
  data() {
    return {
      table1: {
        title: "Simple Table",
        columns: [...tableColumns],
      },
      listProjects: [],
      listCompanies: [],
      company: {},
      store: userStore(),
    };
  },
  methods: {
    async getProjects() {
      this.listProjects = await ProjectsService.getProjects(this.idCompany);
      this.listProjects = this.listProjects.data;
    },
    editProject(item) {
      ProjectsService.updateProp(item);
      document.getElementById("addProjectButton").click();
    },
    FormatDate(date) {
      date = date.split("T");
      date = date[0].split("-").reverse().join("-");
      return date;
    },
    async deleteProject(id) {
      const response = await ProjectsService.deleteProject(id);
      if (response) {
        this.getProjects();
      }
    },
    async getCompanies() {
      this.listCompanies = await CompaniesService.getCompanies();
      this.listCompanies = this.listCompanies.data;
    },
    findCompany(id) {
      for (const company of this.listCompanies) {
        if (company.id == id) {
          return company;
        }
      }
    },
    findType(url) {
      url = url.split("docs/")[1];
      url = url.split(".");
      return url[1];
    },
    findName(url) {
      url = url.split("docs/")[1];
      url = url.split(".");
      return url[0];
    },
    async loadProps() {
      await this.getCompanies();
      if (this.idCompany) {
        this.company = this.findCompany(this.idCompany);
      }
    },
  },
  created() {
    this.loadProps();
    this.getProjects();
    this.rol = this.store.getRole();
  },
};
</script>
