import Api from "./Api";

let project= {
  id: "",
  name: "",
  value: "",
  file: "",
  company: "",
  description: "",
  smalldescription: "",
}
export default {
  getProjects(id) {
    if (id){
      return Api(1).get('projects/'+id+'/company')
    }
    return Api(1).get('projects/')
  },
  addProject(newProject) {
    if (newProject.file){
      newProject.file=newProject.file[0]
    }
    return Api(1,1).post('projects/',
    Object.assign({} , newProject)
    )
  },
  getProp() {
    return Object.assign({} , project)
  },
  updateProp(aux) {
    project=Object.assign({} , aux)
    project.file=null
  },
  resetProp() {
    project= {
      id: "",
      name: "",
      value: "",
      file: "",
      company: "",
      description: "",
      descriptionDetail: "",
    }
  },
  deleteProject(id) {
    return Api(1).delete('projects/' + id)
  },
  updateProject(newProject) {
    if (newProject.file){
      newProject.file=newProject.file[0]
    }
    return Api(1,1).put('projects/' + newProject.id+'/', Object.assign({} , newProject))
  },
}
