<template>
  <div>
    <div class="row justify-content-end">
      <div class="mr-5">
        <button
          id="addCompanyButton"
          class="btn btn-primary"
          @click="
            () => {
              modal.mini = true;
              readyForm();
            }
          "
        >
          {{titles[1]}} Empresa
        </button>
      </div>
    </div>
    <modal
      :show.sync="modal.mini"
      class="modal-primary"
      :show-close="false"
      @close="
      () => {
        resetForm()
      }
    "
      headerClasses="justify-content-center"
      type="mini"
    >
    <alert type="danger" dismissible :visible="visible" :dismissAlert="dismissAlert">
        <!-- <div class="alert-icon">
          <i class="now-ui-icons objects_support-17"></i>
        </div> -->
        <strong>Error al Enviar Empresa</strong> <template>Compruebe Los Campos</template>
    </alert>
      <Card class="mb-0 card-form" :title="titles[1] + ' Empresa'">
        <form  v-on:submit.prevent="HandleCompany()">
          <div class="form-group">
            <label for="example">Nombre</label>
            <input
              type="nombre"
              class="form-control dash"
              placeholder="Ingrese Nombre"
              v-model="company.name"
            />
          </div>
          <div class="form-group">
            <label for="example">Tamaño</label>
            <select class="form-control dash" aria-label="Default select example" v-model="company.size">
              <option value="">Ingrese Tamaño</option>
              <option value="3">Pequeña</option>
              <option value="2">Mediana</option>
              <option value="1">Grande</option>
            </select>
          </div>
          <div class="form-group">
            <label for="example">Logo</label>
            <input
              type="file"
              accept="image/*" 
              class="form-control dash"
              placeholder="Adjunte Logo"
              @change="handleFile"
              ref="logo"
            />
          </div>
          <div class="form-group">
            <label for="example">Descripcion</label>
            <textarea class="form-control dash" placeholder="Ingrese Descripcion" v-model="company.description" />
          </div>
          <!-- <div class="form-check">
            <input
              type="checkbox"
              class="form-check-input"
              id="exampleCheck1"
            />
            <label class="form-check-label" for="exampleCheck1"
              >Check me out</label
            >
          </div> -->
          <div class="row justify-content-center">
            <a class="btn btn-neutral" v-on:click="modal.mini=false; resetForm()" style="color:#f96332">Cerrar</a>
            <button type="submit" class="btn btn-primary">Submit</button>
          </div>
        </form>
      </Card>
    </modal>
  </div>
</template>

<script>
import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import {
  Modal,
  Button,
  FormGroupInput,
  Card , Alert
} from "@/components";
import CompaniesService from '@/services/CompaniesService'
export default {
  name: "FormCompany",
  props: {
    name: String,
    reloadList: { type: Function },
  },
  components: {
    Card,
    Modal,
    [FormGroupInput.name]: FormGroupInput,
    [Button.name]: Button,
    Alert
  },
  setup: () => ({ v$: useVuelidate() }),
  data: () => {
    return {
      // submitt: false,
      company: {
        id: "",
        name: "",
        contact: "",
        size: "",
        description: "",
        logo: File,
      },
      modal: {
        mini: false,
      },
      titles: ["Editar", "Agregar"],
      visible: false,
    };
  },
  validations() {
    return {
      company: {
        id: {},
        name: { required },
        contact: {},
        size: { required },
        description: {required},
        logo: {},
      },
    };
  },
  methods: {
    handleFile(){
      this.company.logo=this.$refs.logo.files
      
    },
    resetForm() {
      CompaniesService.resetProp()
    },
    readyForm() {
      this.$refs.logo.value=null;
      this.company = CompaniesService.getProp()
    },
    async addCompany() {
      const response =await CompaniesService.addCompany(
        this.company.name,
        this.company.size,
        this.company.description,
        '',
        this.company.logo[0]
      )
      if(response){
        this.reloadList()
      }
    },
    async updateCompany() {
      const response=await CompaniesService.updateCompany(
        this.company.id,
        this.company.name,
        this.company.size,
        this.company.description,
        // 2,
        this.company.logo[0]
      )
      if(response){
        this.reloadList()
      }
    },
    HandleCompany() {
      this.v$.$validate();
      if (!this.v$.$error) {
        if (!this.company.id) {
          this.addCompany()
        } else {
          this.updateCompany()
        }
        this.modal.mini=false
        this.resetForm();
      } else {
        this.visible=true
      }
    },
    dismissAlert(){
      this.visible=false;
    }
  },
};
</script>
<style>
.dash:focus {
  background-color: transparent !important;
  color: rgba(255, 255, 255, 0.884)!important;
}
.dash{
  color: rgba(255, 255, 255, 0.6)!important;
}
.dash:disabled{
  background-color: rgb(0 0 0 / 20%) !important;
}
.card-form{
  background: linear-gradient(to bottom left, #27293d00, #27293d, #00000000) !important;
}
</style>
