import { defineStore } from 'pinia';


export const userStore = defineStore('Info', {

    state: () => ({
        token: '',
        id: 0,
        username: '',
        role: 3,
        company: 0,
        numberOfChanges: 0
    }),
    actions: {
        setInfo(id,username,role,company){
            this.id=id;
            this.username=username;
            this.role=role
            this.company=company
        },
        cleanStore(){
            this.token= '',
            this.id= 0,
            this.username= '',
            this.role= 3,
            this.company= 0,
            this.numberOfChanges= 0
        },
        setName(username){
            this.username = username
        },
        setToken(token){
            this.token=token;
            this.numberOfChanges += 1;
        },
        getId(){
            return this.id;
        },
        getName(){
            return this.username;
        },
        getRole(){
            return this.role;
        },
        getCompany(){
            return this.company;
        }
    },
    persist: true,
})